import Vue from 'vue'
import Vuex from 'vuex'
import ui from '../utils/ui.js'

Vue.use(Vuex)

const store = new Vuex.Store({
	state: {
		token: '',
	},
	getters: {},
	mutations: {
		setToken(state, val) {
			state.token = val;
			uni.setStorageSync('token', val);
		},
	},
	actions: {
	}
})

export default store
